const { Player } = TextAliveApp;

let songUrl = 'https://www.youtube.com/watch?v=a-Nf3QUFkOU';

// å˜èªã”ã¨ã«æ­Œè©ã‚’è¡¨ç¤º
const animateWord = (now, unit) => {
  if (unit.contains(now)) {
    $('#text').html(unit.text);
  }
};

const run = () =>{
  //åˆæœŸåŒ–å‡¦ç†
  $('#info').empty();
  $('#media').empty();

  const player = new Player({ app: true , mediaElement: document.querySelector('#media')});
  console.log('Run');

  let c;
  player.addListener({
    onAppReady: (app) => {
      console.log('AppReady');
      player.createFromSongUrl(songUrl);
    },

    // æ¥½æ›²æƒ…å ±èª­ã¿è¾¼ã¿å®Œäº†å¾Œã€å‘¼ã°ã‚Œã‚‹
    // ã“ã®æ™‚ç‚¹ã§ã¯requestPlay()ç­‰ãŒå®Ÿè¡Œä¸èƒ½
    onVideoReady: (v) => {
      console.log('VideoReady');
      let infoContents = '';
      infoContents += '<h1>æ¥½æ›²æƒ…å ±</h1>';
      infoContents += '<h2>æ¥½æ›²åï¼š<br>' + player.data.song.name + '</h2>';
      infoContents += '<h2>ã‚¢ãƒ¼ãƒ†ã‚£ã‚¹ãƒˆåï¼š<br>' + player.data.song.artist.name + '</h2>';
      $('#info').html(infoContents);
      $('#text').html('[å†ç”Ÿæº–å‚™å¾…æ©Ÿä¸­]');
    },

    // å†ç”Ÿæº–å‚™å®Œäº†å¾Œã€å‘¼ã°ã‚Œã‚‹
    // ã“ã‚Œä»¥é™ã€requestPlay()ç­‰ãŒå®Ÿè¡Œå¯èƒ½
    onTimerReady: () => {
      console.log('TimerReady');
      $('#text').html('[å†ç”Ÿé–‹å§‹]');
      player.requestPlay();
      // å®šæœŸçš„ã«å‘¼ã°ã‚Œã‚‹å„å˜èªã® "animate" é–¢æ•°ã‚’ã‚»ãƒƒãƒˆã™ã‚‹
      /*let w = player.video.firstPhrase;
      while (w) {
        w.animate = animateWord;
        w = w.next;
      }*/
    },

    onTimeUpdate: (position) => {
      // æ­Œè©æƒ…å ±ãŒãªã‘ã‚Œã°ã“ã‚Œã§å‡¦ç†ã‚’çµ‚ã‚ã‚‹
      if (!player.video.firstChar) {
        return;
      }
  
      // 500mså…ˆã‹ã‚‰å§‹ã¾ã‚‹æ–‡å­—ï½ãƒ•ãƒ¬ãƒ¼ã‚ºã®æœ€å¾Œã¾ã§å–å¾—
      let current = c || player.video.firstPhrase;
      while (current && current.startTime < position + 500) {
        if (c !== current) {
            let str = current.text;
            let charArray = str.split('');
            for(var i = 0; i < charArray.length; ++i){
              const span = document.createElement("span");
              span.innerHTML = charArray[i];
              $('#text').append(span);
            }
          c = current;
        }
        current = current.next;
      }
    },
  
  });
}

window.run = run